/* eslint-disable no-restricted-globals */
import React from 'react';

import {Spinner, Button, DataTable, DataTableColumn} from '@salesforce/design-system-react';
import SLDSPaginator from './SLDSPaginator';
import GRAXCSVExportButton from './GRAXCSVExportButton';

import {$, baseUrl, GetParams} from '../lib/grax-data-lake';
import {moment, SF_DATETIME_REGEXP, DATE_FORMAT, DATETIME_FORMAT, TZ} from '../lib/utils';

import _ from 'lodash';
import { isArray } from 'util';

const ROWSXPAGE = 100;

export default class GRAXRestoreDataTable extends React.Component {
	constructor(props) {
        super(props);

        this.state = {
			sortColumn: 'Id',
			sortDirection: 'asc',
            chunks: [],
            items: [],
            extraInfo: {},
            page: 0,
            selection: [],
            isLoaded: false,
            isLoading: false
		};
    }

    shouldComponentUpdate(nextProps, nextState) {
        return this.props.renderKey !== nextProps.renderKey || this.state.items !== nextState.items || this.state.selection !== nextState.selection || this.state.isLoaded !== nextState.isLoaded || this.state.isLoading !== nextState.isLoading || this.state.sortColumn !== nextState.sortColumn || this.state.sortDirection !== nextState.sortDirection || this.props.columns !== nextProps.columns;
    }

    getDataHandler(page, sortColumn, sortDirection) {
        this.setState({
            isLoading: true,
            page: page,
            sortColumn: sortColumn,
            sortDirection: sortDirection
        });

        $.ajax({
            type: 'GET',
            //dataType: 'jsonp',
            url: baseUrl + '/grax-app/grax-data-lake' + GetParams({
                action:'load',
                dataIndex: this.props.dataIndex,
                _select: _.union(this.props.columns, this.props.hiddenColumns).join(',') || 'Id',
                _q: this.props.filterQuery,
                _limit: ROWSXPAGE,
                _from: ROWSXPAGE * page,
                _sort: sortColumn + ' ' + sortDirection,
                _sortType: this.getSortFieldType(sortColumn)
            }),
            /*data: {
                _select: this.props.columns.join(',')
            },*/
            success: function(data) {
                var results = data.results;

                if(this.props.filterFunction) {
                    results = this.props.filterFunction(results);
                }

                results = _.map(results, (item, i) => {
                    _.forEach(item, (val, key) => {
                        if(typeof(val) == 'string') {
                            if(SF_DATETIME_REGEXP.test(val)) {
                                var m = moment(val);

                                if(m.isValid()) {
                                    item[key] = m.tz(TZ).format(m._i.indexOf(':') !== -1 ? DATETIME_FORMAT : DATE_FORMAT);
                                }
                            }
                        }

                        if(isArray(val)) {
                            item[key] = val.join(', ');
                        }

                        if(typeof(val) == 'boolean') {
                            item[key] = val ? 'true' : 'false';
                        }
                    });

                    return {
                        ...item,
                        ...{id: i.toString()}
                    }
                });

                this.setState({
                    page: 0,
                    data: results,
                    extraInfo: data.extraInfo || {total: 0, size: ROWSXPAGE, totalRetrieved: 0},
                    items: results,
                    isLoaded: true,
                    isLoading: false
                });
            }.bind(this),
            error: function(xhr, status, err) {
                console.error(xhr, status, err);

                this.setState({
                    isLoaded: true,
                    isLoading: false,
                    items: [],
                    error: err.toString()
                });
            }.bind(this)
        });
    }
    
    componentDidMount() {
        this.getDataHandler(0, this.state.sortColumn, this.state.sortDirection);
    }

	handleChanged(event, data) {
		this.setState({ selection: data.selection });
		console.log(event, data);
	}

	handleSort(sortColumn, ...rest) {        
        if(sortColumn.property) {
            this.getDataHandler(this.state.page, sortColumn.property, sortColumn.sortDirection);
        }
    }
    
    getSortFieldType(sortField) {
        if(!this.props._graxFieldsSelection) return null;

        return (this.props._graxFieldsSelection.find((item) => {
            return item.id === sortField;
        }) || {}).type;
    }

	render() {
		return this.state.isLoaded ? (
			<div>
                <div style={{position: 'relative'}}>
                    {this.state.isLoading && <Spinner size="large" variant="brand" />}
                    <DataTable
                        assistiveText={{
                            actionsHeader: 'actions',
                            columnSort: 'sort this column',
                            columnSortedAscending: 'asc',
                            columnSortedDescending: 'desc',
                            selectAllRows: 'all rows',
                            selectRow: 'select this row',
                        }}
                        fixedLayout
                        items={this.state.items}
                        id="GRAXRestoreDataTable"
                        onRowChange={this.handleChanged.bind(this)}
                        onSort={this.handleSort.bind(this)}
                        selection={this.state.selection}
                        selectRows={true}
                    >

                        {this.props.columns.map((columnName) => (
                            <DataTableColumn
                                key={'col' + columnName}
                                id={columnName}
                                isSorted={this.state.sortColumn === columnName}
                                label={columnName} // TODO
                                property={columnName}
                                sortable={this.props.sortableColumns == null || this.props.sortableColumns.indexOf(columnName) !== -1}
                                sortDirection={this.state.sortDirection}
                            />
                        ))}
                    </DataTable>
                </div>

                <div className="-x-small-buttons--horizontal slds-p-top_small">
                    <SLDSPaginator totalItems={this.state.items.length > 0 ? this.state.extraInfo.total: 0} pageSize={ROWSXPAGE} onChangePage={(page, isInitialLoad) => {
                        if(!isInitialLoad) {
                            this.getDataHandler(page - 1, this.state.sortColumn, this.state.sortDirection);
                        }
                    }} />
                </div>

                <div className="-x-small-buttons--horizontal slds-p-top_medium">
                    <Button label="Restore Selection" disabled={this.state.selection.length < 1 || this.state.isLoading} onClick={() => {
                        if(confirm('Are you sure that you want to re-create the selected records?')) {
                            $.ajax({
                                type: 'POST',
                                url: baseUrl + '/grax-app/grax-data-lake' + GetParams({
                                    action: 'restore',
                                    restoreChildrens: this.props.restoreChildrens,
                                    sfRequestsDisabled: this.props.sfRequestsDisabled,
                                    checkIsUserActive: this.props.checkIsUserActive,
                                    useGRAXHistoryOnly: this.props.useGRAXHistoryOnly,
                                    updateRecordIfExist: this.props.updateRecordIfExist,
                                    sendNotification: this.props.sendNotification
                                }),
                                contentType: "application/json; charset=utf-8",
                                data: JSON.stringify(_.map(this.state.selection, function(item) {
                                    return _.omit(item, ['id']);
                                })),
                                success: function(data) {
                                    alert(data.state);
                                }
                            });

                            this.setState({
                                selection: []
                            });
                        }
                    }} />

                    <Button label="Restore All" disabled={this.state.isLoading} onClick={() => {
                        if(confirm('Are you sure that you want to re-create the filtered records?')) {
                            $.ajax({
                                type: 'POST',
                                url: baseUrl + '/grax-app/grax-data-lake/restoreAll' + GetParams({
                                    action: 'restore',
                                    restoreChildrens: this.props.restoreChildrens,
                                    sfRequestsDisabled: this.props.sfRequestsDisabled,
                                    checkIsUserActive: this.props.checkIsUserActive,
                                    useGRAXHistoryOnly: this.props.useGRAXHistoryOnly,
                                    updateRecordIfExist: this.props.updateRecordIfExist,
                                    sendNotification: this.props.sendNotification
                                }),
                                contentType: "application/json; charset=utf-8",
                                data: JSON.stringify({
                                    dataIndex: this.props.dataIndex,
                                    _q: this.props.filterQuery,
                                    _sort: this.state.sortColumn + ' ' + this.state.sortDirection,
                                    _sortType: this.getSortFieldType(this.state.sortColumn)
                                }),
                                success: function(data) {
                                    alert(data.state);
                                }
                            });

                            this.setState({
                                selection: []
                            });
                        }
                    }} />

                    <GRAXCSVExportButton params={{
                        source: 'elastic',
                        dataIndex: this.props.dataIndex,
                        _select: _.union(this.props.columns, this.props.hiddenColumns).join(','),
                        _q: this.props.filterQuery,
                        _sort: this.state.sortColumn + ' ' + this.state.sortDirection,
                        _sortType: this.getSortFieldType(this.state.sortColumn),
                        columns: this.props.columns
                    }} />
                </div>
			</div>
		) : (
            <div style={{ position: 'relative', height: '2rem' }}>
                <Spinner size="medium" variant="brand" />
            </div>
        );
	}
}